import * as React from "react";
import { Link } from "gatsby";
import logoImg from "../images/Lane7_Green.svg";

const Header = ({ pageTitle, children }) => {
  return (
    <div class="container-fluid">
      <div class="row p-2 align-items-center">
        <div class="col-xs-12 col-md-4 align-self-center">
          <div className="nav text-bg-light">
            <Link
              to="/"
              className="nav-link text-bg-light active"
              aria-current="page"
            >
              Home
            </Link>
            <Link to="/" className="nav-link text-bg-light" aria-current="page">
              Work
            </Link>
            <Link to="/" className="nav-link text-bg-light" aria-current="page">
              About
            </Link>
            <Link to="/" className="nav-link text-bg-light" aria-current="page">
              Process
            </Link>
          </div>
        </div>
        <div class="col-xs-12 col-md-4 text-center">
          <Link to="/" className="navbar-brand">
            <img
              alt="Lane7"
              src={logoImg}
              width={200}
              height={120}
              className="d-inline-block align-text-top"
            />
          </Link>
        </div>
        <div class="col-xs-12 col-md-4 text-sm-center text-md-end">
          hello@lane7.com
        </div>
      </div>
    </div>
  );
};

export default Header;
